                "rdLatitude": 39.9117565763,
                "type": 2,
                "content": "游客您好，现在您来到的中山堂是社稷坛中轴线上的重要建筑。拜殿为一处歇山黄琉璃瓦屋面，饰以金龙合玺彩画，是皇帝祭祀日遇风雨行礼的地方，据载明代以前历代社稷坛均不设拜殿，明太祖朱元璋觉得这样有失帝王威仪，便下旨兴建拜殿。《北京古建筑》一书载：“史料中未发现被毁或重建的记录，可能是北京现存明代建筑中最古老的一座”，因此，也是重点文物保护建筑。1925年，孙中山先生逝世后曾在这里停灵，1928年河北省政府和北平特别市政府决定将拜殿改为中山堂。新中国成立后在这里先后召开过四届、十二次北京市各界人民代表会议。现在，每年3月12日和11月12日首都各界人士都要在此隆重举行孙中山诞辰和忌辰活动。",
