                "content": "各位游客，现在您来到了位于内坛东北角的社稷祭坛。它建于明永乐十八年，位于内坛核心区，正方形、三层，四面各设四步台阶。上层按东青、南红、西白、北黑、中黄，铺设五色坛土，俗称“五色土”，象征“普天之下，莫非王土”，坛台中央立“社主石”，亦称“江山石”。明清两朝皇帝每年的农历二月、八月上戊日例行在此举行祭祀仪式，祈求五谷丰登、国泰民安。坛台四周建有矮墙，也称壝垣（w&eacute;iyu&aacute;n），明朝初建时是用砖砌的，后涂上青、赤、白、黑色。清乾隆二十一年，将壝垣改为用四色琉璃砖、瓦砌成，与坛台相呼应。壝垣四面各有一个棂星门，白色石块筑成，每门原有棂星式朱漆门两扇，经过战乱，现在已经不存在了。",
