                "content": "欢迎您参观习礼亭，习礼亭为黄琉璃瓦顶，六角形，建在两层台基上，南北两面石阶，北阶3步，南阶6步。亭内有金龙仿心旋子彩画，朱棂门窗，面朝北一方为隔扇门，其余为槛（kǎn）窗。该亭建于明永乐十八年，原址在正阳门内兵部街鸿胪寺衙门内，是明、清两朝专为初次来京的文武官员、少数民族首领和附属国贡使等演习朝觐皇帝礼仪之用，故名习礼亭，亦称演礼亭。光绪二十六年八国联军入侵北京时，鸿胪寺被焚，此亭幸免。不久英军强占鸿胪寺做操场，清政府被迫将习礼亭迁至户部街礼部衙门院中,清末，礼部改为典礼院。1912年典礼院为盐务署占驻，1915年4月移建此亭到中央公园。",
