                "type": 2,
                "content": "游客您好，这里是以弘扬中国兰花文化为主的蕙芳园，其原址是公园的育花温室区。蕙芳园于1988年动工兴建，1990年9月开放，占地面积7600平方米，建筑面积986平方米。蕙芳园的建设，既体现了中国园林景观的民族特色，又突出了兰花为主的特点。该园设计采用封闭式的造园手法，用竹和松篱分隔空间，通过地形变换，植物疏密，造成一个空谷山幽，茂林修竹，萦绕曲折，清雅静谧的自然景观。景区分为4个部分。一区从入口至“柴门”以南为序幕区，40米长的竹林，曲折蜿蜒，可寻芳探幽。二区是入柴门往北至四合院以南为主景区。以黄土铺垫成小丘陵地貌，用山石造成山势，遍植各种植物，起伏错落，层次分明。三区为展览区，是一组北方民居形式的四合院。南北房各5间，东西房各3间，硬山黑瓦卷棚屋面。南房明间门楣上悬挂着朱德委员长题写的“兰室”匾额，院中置太湖石1座。四区为育花区。",
