                "type": 2,
                "content": "游客您好，现在您来到的是来今雨轩。它建于1915年，位于北京中山公园东侧，坐北朝南，当年门上的匾额来今雨轩四个大字是徐世昌手笔，门前曾有楹联：三篇陆羽经，七度卢仝（t&oacute;ng）碗；和茶社很是贴切。现在原来的匾额和楹联都已不见，楹联变成：莫放春秋佳日过最难风雨故人来。来今雨轩是民国时期社会名流休闲聚会的场所；张恨水、老舍、林徽因、徐志摩、巴金、鲁迅等都曾在此聚会甚至写稿。到了上个世纪80年代后期，在中山公园西侧新建仿古建筑，主楼二层，坐西朝东，有山石、水池、游廊、花木点缀，名为来今雨轩饭庄，主营红楼宴；许多人久仰来今雨轩大名，到中山公园访古，以为中山公园西侧的来今雨轩饭庄就是当年名流聚会的地方，这是不对的。目前，位于中山公园西侧的新建来今雨轩饭庄，“停业整修”多年，2014年1月1日再次出了通告，声称：何时开业，请等待通知。东侧的则恢复为茶社，并于大厅门前以及门廊西侧，悬挂铜质名牌，介绍了来今雨轩茶社的史实。",
