                "content": "欢迎您参观水榭景区。水榭建于1916年夏秋间，在唐花坞前一片荷塘南岸建北厅3间，东、西厅各建3间，环厅内外建游廊，均为砖木结构，卷棚筒瓦屋面。南面建垂花门1座，左右筑花墙，独成一庭院。北厅地基一半在水中，有地下室2间，北半部环厅游廊以水泥柱墩支架于水面之上，为三合院式水榭。1928年春，将南面垂花门及花墙拆除，增建南厅5间，歇山式灰瓦顶，砖木结构，内外走廊，四面厅房与廊连接，扩建工程于1928年10月竣工，建筑面积818.7平方米。外廊南面沿廊柱设坐凳，东、西、北三面沿外廊柱设靠栏坐凳。原公园董事恽宝惠曾手书“水榭”匾额，现已摘除。",
