                "isIndoor": 0,
                "rdLatitude": 39.9094317379,
                "type": 2,
                "content": "各位游客，这一块巨大的太湖石是“青云片”，原是圆明园四十景之一“别有洞天”景区内时赏斋前的遗物。“青云片”和“青芝岫”原都是明万历年进士、书画家米万钟的爱物。米万钟是大书法家米芾（f&uacute;）的孙子，家中十分阔绰。米万钟喜欢画石，也喜欢到处寻找奇石。“青云片”和“青芝岫”均采自京西南房山大石窝山中，米万钟原想将它们移至勺园，后因家财耗尽而弃于房山良乡山野中。多年后，二石被乾隆发现，将“青芝岫”运到了颐和园，“青云片”运到了圆明园时赏斋。乾隆对“青云片”十分喜爱，曾于乾隆三十一年亲自为“青云片”题写名字并做御诗八首。诗中云：“当门湖石秀屏横，坐喜松阴满砌清。时赏试言应为好，树姿花意盼春晴。”圆明园因遭八国联军焚毁而荒废，为了更好地保护和展示这一园林瑰宝，“青云片”石1925年由圆明园移至中央公园，置于来今雨轩长廊以西路旁。1971年后移至今天的位置。石上的刻字大部分因年代久远而模糊，但名石依旧身姿秀美，空穴明晰，结构奇巧，玲珑剔透，似烟云缭绕，是北京园林名石之一。",
